                // },
                // ["Safari", 8.5],
                // ["Opera", 6.2],
                // ["其他", 0.7]
              ]
            }
          ]
        },
        function(c) {
          // 环形图圆心
          var centerX = c.series[0].center[0];
          var centerY = c.series[0].center[1];

          // 标题字体大小，返回类似 16px ，所以需要 parseInt 处理
          var titleHeight = parseInt(c.title.styles.fontSize);

          // 设置图表偏移
          c.setTitle({
            y: centerY + titleHeight / 2
          });
        }
      );
    }
  }
